import { RelatiGrid } from "../RelatiBoard";
import { RelatiGame } from "../RelatiGame";
import { RelatiRouteRule } from "./RelatiRouteRule";
import { RelatiSymbol, RelatiRule, RelatiStatus, RelatiRouteType } from "../RelatiDefs";

let placementStatus: RelatiStatus[] = ["relati-repeater"];

/**  */
interface PlacementRule extends RelatiRule {
    /**
     * 
     * @param grid 
     * @param symbol 
     * @param routeType 
     */
    allow(
        grid: RelatiGrid,
        symbol: RelatiSymbol,
        routeType: RelatiRouteType
    ): boolean;

    /**
     * 
     * @param game 
     * @param symbol 
     */
    trace(
        game: RelatiGame,
        symbol: RelatiSymbol,
        routeType?: RelatiRouteType
    ): RelatiGrid[];
}

/**  */
export let PlacementRule: PlacementRule = {
    /**
     * 
     * @param grid 
     * @param symbol 
     * @param routeType 
     */
    allow(grid, symbol, routeType) {
        return grid.isSpace && RelatiRouteRule.allow(
            grid, symbol, placementStatus, routeType
        );
    },

    /**
     * 
     * @param game 
     * @param symbol 
     * @param routeType 
     */
    trace(game, symbol, routeType?) {
        let grids: RelatiGrid[] = [];

        for (let grid of game.board.grids) {
            if (PlacementRule.allow(
                grid, symbol, routeType || game.routeType
            )) grids.push(grid);
        }

        return grids;
    }
};